import React, { useState } from 'react'
import { Link, Navigate, NavLink } from 'react-router-dom'
import { ToastContainer, toast } from 'react-toastify'
import Api from '../services/api'
import { useAuth } from '../contexts/AuthContext'
import logoLegalistas from '../assets/images/logos/logo-legalistas.svg';

import LogoIcon from '../components/LogoIcon'
import GoogleIcon from '../components/icons/Google'

export default function Login() {
    const { setUser, csrfToken } = useAuth()
    const [errors, setError] = useState(null)

    // login user
    const handleSubmit = async (e) => {
        e.preventDefault()
        const { email, password } = e.target.elements
        const body = {
            email: email.value,
            password: password.value,
        }
        await csrfToken()
        try {
            const resp = await Api.post('/login', body)
            if (resp.status === 200) {
                setUser(resp.data.user)
                return <Navigate to="/profile" />
            }
        } catch (errors) {
            if (errors.response) {
                if (errors.response.status === 401) {
                    toast.error('Email o contraseña incorrectos')
                } else if (
                    errors.response.status === 422 &&
                    errors.response.data.errors &&
                    errors.response.data.errors.email
                ) {
                    const errorMessage = errors.response.data.errors.email[0]
                    toast.error(errorMessage)
                } else if (errors.response.status === 404) {
                    toast.error('No se encontró la página solicitada')
                }
            } else {
                console.error('Error durante el inicio de sesión:', errors)
            }
        }
    }

    const gradient = {
        backgroundImage:
            'linear-gradient(90deg, rgba(7,133,142,0.7175245098039216) 25%, rgba(9,167,178,1) 75%)',
        overflowY: 'hidden',
        maxWidth: '100%',
    }

    const maxHeight = {
        height: '100vh',
    }

    return (
        <div className="position-relative" style={gradient}>
            <LogoIcon className="legalistas-icon-top" />
            <LogoIcon className="legalistas-icon-bottom" />
            <ToastContainer />
            <div className="pt-2 pt-sm-5 pb-4 pb-sm-5" style={maxHeight}>
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-xxl-4 col-xl-5 col-lg-6 col-md-8 col-xs-6 col-sm-12 mx-auto">
                            <img
                                alt="Legalistas "
                                width={340}
                                height={80}
                                src={logoLegalistas}
                                className="d-block mx-auto mb-4"
                            />

                            <div className="card p-4 shadow">
                                <div className="card-header">
                                    <div className="text-center w-100 m-auto">
                                        <h1 className="text-muted text-center pb-0 fw-bold fs-7">
                                            Inicia sesión
                                        </h1>

                                        <p className="welcomeText text-muted">
                                            ¡Bienvenido, podrás iniciar sesión
                                            fácilmente con los detalles que te
                                            proporcionaremos.!
                                        </p>
                                    </div>
                                </div>
                                <div className="card-body">
                                    <form
                                        action="#"
                                        method="post"
                                        onSubmit={handleSubmit}
                                    >
                                        <div className="form-group mb-1">
                                            <label
                                                htmlFor="email"
                                                className="form-label fw-normal fs-8 text-muted"
                                            >
                                                Correo electrónico
                                            </label>
                                            <input
                                                id="email"
                                                className="form-control"
                                                required
                                                autoComplete="email"
                                                autoFocus
                                                name="email"
                                                type="email"
                                                placeholder="Correo electrónico"
                                            />
                                        </div>
                                        <div className="form-group mb-4">
                                            <label
                                                htmlFor="password"
                                                className="form-label fw-normal fs-8 text-muted"
                                            >
                                                Contraseña
                                            </label>
                                            <input
                                                id="password"
                                                className="form-control"
                                                required
                                                autoComplete="password"
                                                autoFocus
                                                name="emappasswordasswordil"
                                                type="password"
                                                placeholder="Ingrese su contraseña"
                                            />
                                        </div>

                                        <p className="text-center fs-9 d-flex align-items-center justify-content-center text-muted">
                                            ¿Olvidaste tu contraseña?
                                            <NavLink
                                                to="/forgot-password"
                                                exact="true"
                                                className="ms-2 text-muted"
                                            >
                                                Recuperar contraseña
                                            </NavLink>
                                        </p>

                                        <button className="btn btn-primary btn-lg fw-normal w-100">
                                            Iniciar sesión
                                        </button>

                                        <p className="text-center fs-9 mt-4 mb-2 text-muted">
                                            o inicia sesión con:
                                        </p>
                                        <button className="btn btn-lg btn-outline-light justify-content-center d-flex align-items-center w-100 ">
                                            <GoogleIcon
                                                width="16"
                                                height="16"
                                            />
                                            <span className="text-dark">
                                                Inicia sesión con Google
                                            </span>
                                        </button>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
